import React, { Component } from 'react';
import axios from 'axios';
import { Col, Row, Container} from 'react-bootstrap'
import { Formik } from "formik";
import * as Yup from "yup";
import swal from "sweetalert";
import Header from './header'
const InviteSchema = Yup.object().shape({
    email: Yup.string()
      .required("Email address is required")
  });

class Invite extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
          alert: null,
          loaded: false
        };
      }

    submitForm = (values, history) => {
      values.email = values.email.toLowerCase()

        axios
          .post('/invite', values)
          .then(res => {
            if (res.data.result === "success") {
              swal("Success!", res.data.message, "success").then(value => {
              });
            } else if (res.data.result === "error") {

              swal("Error!", res.data.message, "error");

            }
          })
          .catch(error => {
            console.log(error);
            swal("Error!", "Unexpected error", "error");
          });
      };


    showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        isSubmitting
      }) => {

    return(
     
<form onSubmit={handleSubmit}>
      <div className="loginRegisterTitle">
            </div>
        <div className="emailfield">
          <input
            type="email"
            name="email"
            onChange={handleChange}
            value={values.email}
            className={
              errors.email && touched.email
                ? "form-control is-invalid"
                : "form-control"
            }
            placeholder="Email"
            style={{marginTop:'25px'}}

          />
          {errors.email && touched.email ? (
            <small id="passwordHelp" className="text-danger">
              {errors.email}
            </small>
          ) : null}
        </div>
        
        <div className="row">
          <div className="col s8 offset-s2 center">
            <button
              type="submit"
              disabled={isSubmitting}
              className="btn btn-primary"
              style={{marginTop:'25px'}}
            >
              Send
            </button>

          </div>
     
        </div>
      </form> 

    )
      }
       
          render() 
          {
              return(
                <div className="contactdiv">
                    <Header />
                <img src={`https://keithlovesmovies.com/wp-content/uploads/2019/04/This-is-Us.jpg`} alt={`poster`} style={{width:'100%'}} />
           <Container style={{backgroundColor:"#eee",paddingBottom:"7%"}}>
               
               <Row md={12} className="justify-content-md-center" style={{width:"100%", textAlign:"center"}}>
   
   <p style={{marginTop:'7%'}}>Lastplay was built thanks to you. </p><p>The journey has been a thrill for our team to become a small piece of your daily adventure. Together we will continue to grow our family and we hope to touch each and every one of you. Below you can invite a special someone to join us in LastPlay. We will send a special invitation to the email you have filled in.
   </p>
   <span  style={{fontStyle:"italic", fontSize:"12px"}}>“I don’t know what I think we are. I just know that I like the fact that there’s a ‘we’ for us to talk about.” — Kevin Pearson</span>

   <Col md={6} className="justify-content-center">

                  <Formik
                    initialValues={{
                      email: "",
                      username: localStorage.userName
                      
                    }}
                    onSubmit={(values, { setSubmitting, resetForm }) => {
                      this.submitForm(values, this.props.history);
                      setSubmitting(false);
                      resetForm({values:''})
                    }}
                    validationSchema={InviteSchema}
                  >
                    {props => this.showForm(props)}
                  </Formik>
                  </Col>
</Row>
<Row style={{paddingTop:"10%"}}className="justify-content-md-center">
    
</Row>

      </Container>
      </div>
  
              )
          }
        }

export default Invite;