import React, { Component } from 'react'
import swal from "sweetalert";
import { withRouter, Link } from "react-router-dom";
import { Nav, Navbar, NavDropdown, ListGroup } from 'react-bootstrap'
import '../style.css'
import Autocomplete from 'react-autocomplete';
import Cookies from 'js-cookie'




let menuStyle={
  borderRadius: '',
  boxShadow: '0 2px 12px rgba(0, 0, 0, 0.1)',
  background: 'rgba(255, 255, 255, 0.9)',
  padding: '0 0',
  fontSize: '90%',
  position: 'fixed',
  overflow: 'auto',
  paddingBottom:'-5px',
  maxHeight: '50%', // TODO: don't cheat, let it flow to the bottom
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      fetched: false,
      shows: [],
      movies: [],
      names: ''
    };
  }

  getFilteredItems =(props) =>{
    const items = Autocomplete.prototype.getFilteredItems.call(this, props);
    return items.slice(0, props.limitItemsLength);
  }
 Logout = props => {
  swal("Logout?", {
    buttons: {
      nope: {
        text: "Yes",
        value: "yes"
      },
      sure: {
        text: "Not yet",
        value: "no"
      }
    }
  }).then(value => {
    switch (value) {
      case "yes":
          Cookies.remove('TOKEN_KEY')
           this.props.history.push("/login");
        ;
        break;
      case "no":
        break;
      default:

    }
  
})}

componentDidMount() {
  const user = Cookies.get('name')
  this.setState({name: user})
  let urls = [fetch(`https://lp-server1.lastplay.tv/app/api/series?apikey=${process.env.REACT_APP_SONARR}`),
              fetch(`https://lp-server1.lastplay.tv/mov/api/movie?apikey=${process.env.REACT_APP_RADARR}`)]
  Promise.all(urls, user)
  .then(([res1, res2]) => { 
      return Promise.all([res1.json(), res2.json()]) 
   })
   .then(([res1, res2]) => {
    this.setState({fetched: true})
    res1.map(show => {
      show['division'] = 'tv'
      return this.state.shows.push(show) })
    res2.map(movie => {
      movie['division'] = 'movie'

      return (this.state.shows.push(movie))

    })
  })


}


render() 
{
  return(
    <div>

    <Navbar fixed="top" bg="dark" variant="dark" expand="lg">
    
    <Navbar.Brand href="/dashboard"><img src={process.env.PUBLIC_URL + '/logo100.png'}className="headerimg"alt="logo"/><b>Last</b>Play</Navbar.Brand>
    
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
    
    
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="mr-auto">
  

                <Nav.Link href="/dashboard">Home</Nav.Link>
                <Nav.Link href="/dashboard">TV</Nav.Link>
                <Nav.Link style={{marginRight:"5px"}}href="/movies">Movies</Nav.Link>

                <Nav className="searchbar">
                <ListGroup variant="flush">
      <Autocomplete
                   
                   items={ this.state.shows} 
                   shouldItemRender={(item, value) => item.title.toLowerCase().indexOf(value.toLowerCase()) > -1 && value.slice(1,10)}
                   getItemValue={item => item.title}
                   renderItem={(item, highlighted) =>
                    <ListGroup.Item  className="searchbar">
                      {
                        item.division === 'tv' &&
                      
                     <a
                       key={item.id}
                       className="stretched-link dropdowns"
                       href={`/tv/${item.id}`}
                     > <img src={`https://lp-server1.lastplay.tv/app/MediaCover/${item.id}/poster-250.jpg`} style={{width:'42px', height:"75px"}}alt={`${item.title} poster`}/>
                        {item.title}
                      </a> }

                      {
                        item.division === 'movie' && item.hasFile === true &&
                      
                     <Link
                       key={item.id}
                       className="stretched-link dropdowns"
                       to={{
                        pathname:`/movie/${item.id}`, state: {
                                tmd: item.tmdbId}}}
                     > <img src={`https://lp-server1.lastplay.tv/mov/MediaCover/${item.id}/poster-250.jpg`} style={{width:'42px', height:"75px"}}alt={`${item.title} poster`}/>
                        {item.title}
                      </Link> }

                     </ListGroup.Item>
                   
                   }
                   value={this.state.value}
                   onChange={e => this.setState({ value: e.target.value })}
                   onSelect={value => this.setState({ value })}
                   onClick={value => this.setState({ value })}
                   menuStyle={menuStyle}
                   inputProps={{placeholder: "Search.."}}
                   
                   
                 /></ListGroup>
      </Nav>
      
              </Nav>
              
                <Nav>
                <Nav.Link href="/invite">Invite</Nav.Link>
                <Nav.Link href="/contact">Contact</Nav.Link>

                <NavDropdown title={`Welcome, ${this.state.name}`} id="collasible-nav-dropdown">
                  <NavDropdown.Item bg="dark" href="#" onClick={this.Logout}>Logout</NavDropdown.Item>
            </NavDropdown>
            
              </Nav>
            </Navbar.Collapse>
          </Navbar>
                </div>


  )
}

  }
  

export default withRouter(Header);


