import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Col, Row} from 'react-bootstrap'
import '../style.css'
import Header from '../header'
import NewShows from './newshows.js'

class Dashboard extends Component {
    constructor() {
        super()
        this.state = {
            shows: []

        }
    }

componentDidMount() {
    fetch(`https://lp-server1.lastplay.tv/app/api/series?apikey=${process.env.REACT_APP_SONARR}`)
    .then(res => res.json())
    .then((data)=> {
        this.setState({ 
            shows: data.sort((a, b) => a.title.localeCompare(b.title  ))
        })
    })
    .catch(console.log)
}


render() 
{
    return (<div>
<Header />
<NewShows />
        <div>
            
          <Row style={{marginTop:"5%"}} className="accordian justify-content-center">
              
            {this.state.shows.map(show =>
            <Col xs={6} sm className='shows' key={`${show.id}`}>
                <Link to={`tv/${show.id}`}>
                    <img src={`https://lp-server1.lastplay.tv/app/MediaCover/${show.id}/poster-250.jpg`} alt={`${show.title} poster`}  /> <br />
                    <span>{show.title}</span>
                </Link>
            </Col>)}
          </Row>
        </div>
        </div>

    )
    }
}
export default Dashboard