import React from 'react';
import './style.css';
import JSPlayer2 from './playermov'
import "react-multi-carousel/lib/styles.css";
import Header from './header'
import "./movie.css"
import { Button, Col, Row, Container} from 'react-bootstrap'
import ReactPlayer from 'react-player'
import "video-react/dist/video-react.css";
import Report from './videofunctions/report.js'

import {
    MobileView,
    isMobile, 
    isTablet,
    isBrowser,
    isSmartTV
  } from "react-device-detect";

  const OS = require('opensubtitles-api');
const OpenSubtitles = new OS({
    username:'nameisadrian',
    password:'7927471aF',
    useragent:'TemporaryUserAgent',

    ssl: true
});

const language = ['eng', 'spa']


class MovDetail extends React.Component {
    constructor(props) {
        super();

        this.state = {
            movie: [],
            info:[],
            poster: '',
            loaded:false,
            itemId: null
        }

        this.togglePlayer = this.togglePlayer.bind(this)
        this.toggleTrailer = this.toggleTrailer.bind(this)



    };

    toggleTrailer = () => {
        this.setState ({ trailer: '1',
                          player: false})

    }
    togglePlayer = () => {
        this.setState ({ player: true,
                         trailer: '2'   })

    }



    componentDidMount() {
        fetch(`https://lp-server1.lastplay.tv/mov/api/movie/id=${this.props.match.params.id}?apikey=${process.env.REACT_APP_RADARR}`)
        .then(res => res.json())
        .then((data)=> {
            data.path = data.path.replace('media/disk3/', '')
            this.setState({ 
                info: data
            })
            OpenSubtitles.search({
                sublanguageid: language.join(),       // Can be an array.join, 'all', or be omitted.
            
                extensions: ['srt', 'vtt'], // Accepted extensions, defaults to 'srt'.
                limit: '3',                 // Can be 'best', 'all' or an
                imdbid: this.state.info.imdbId,           // 'tt528809' is fine too.
            
            }).then(subtitles => {
                // an array of objects, no duplicates (ordered by
                // matching + uploader, with total downloads as fallback)
                console.log(subtitles)
            });

        return fetch(`https://api.themoviedb.org/3/movie/${this.state.info.tmdbId}?api_key=${process.env.REACT_APP_TMDB}`)
        })
        .then(res => res.json())
        .then((data)=> {
            this.setState({ 
                movie: data
            })
        })
    }

    render() 
    {
        return (
<Container fluid className="UpperHeader2" style={{backgroundImage:"url(" + "http://image.tmdb.org/t/p/original" + this.state.movie.backdrop_path + ")", backgroundSize: 'cover'}}>
    <Header /> 
    <Row xs={12}>
        <Col lg={4} className="DetailPanel ">
            <img src={`http://image.tmdb.org/t/p/w342${this.state.movie.poster_path}`} style={{width:"75%"}}alt={`${this.state.movie.title} poster`} /> 
           <Row className="float-right">
            <Report stream="Movie" location={this.state.movie.title} user={localStorage.userName} platform={navigator.userAgent} vendor={navigator.vendor}/>
            </Row>
            <h2 style={{marginTop:"5%"}}>{this.state.movie.title}</h2>
            <span style={{ fontSize:"12px", color:"#b7b7b7"}}>{this.state.info.year}{' '} | {''}{(this.state.movie.runtime / 60).toFixed(1)} hrs </span><br />        
        <MobileView>{ (isMobile || isTablet) && this.state.trailer ==='1' &&

       
            
            <ReactPlayer className="player" url={`https://www.youtube.com/watch?v=${this.state.info.youTubeTrailerId}`}
            playing
            controls={true}
            playsinline={false} 
            width="350px"
            height="196px"
            /> }

            { (isMobile || isTablet) && this.state.trailer ==='2'&& <Row className="player justify-content-center">


            <JSPlayer2 source={`https://lp-server1.lastplay.tv${this.state.info.path}/${this.state.info.movieFile.relativePath}`}
            playsinline={false} 
            controls={true}
            width={350}
            height={196}
            /> </Row>}

        </MobileView>
        <span> {this.state.movie.overview}</span> <br />
        <Button  style={{marginTop:"5%"}}variant="outline-danger" onClick={ this.toggleTrailer }>Watch Trailer</Button>{' '}<br />
        <Button style={{marginTop:"5%"}} variant="outline-primary" onClick={ this.togglePlayer }>Watch Movie</Button>{' '}
        
        </Col>
<Row className="justify-content-center">
            {(isBrowser || isSmartTV)  && this.state.trailer ==='1' && 
            <ReactPlayer className="player" url={`https://www.youtube.com/watch?v=${this.state.info.youTubeTrailerId}`}
            playing
            controls={true}
            playsinline={false} 
            width="960px"
            height="540px"
            /> }

            {(isBrowser || isSmartTV)  && this.state.trailer ==='2'&& <Row className="player justify-content-center">


            <JSPlayer2 source={`https://lp-server1.lastplay.tv${this.state.info.path}/${this.state.info.movieFile.relativePath}`}
            playsinline={false} 
            controls={true}
            width={960}
            height={540}
            /> </Row>}
</Row>
    </Row>
</Container>
    
        )
        }
    }
    

export default MovDetail;