import React, { Component } from "react";
import Register from "./components/register";
import Login from "./components/login";
import Dashboard from "./components/dashboard/index.js";
import Video from "./components/video";
import Movie from "./components/movie";
import Contact from "./components/contact";
import Invite from "./components/invite";
import MovDetail from "./components/mov";
import Cookies from 'js-cookie'
import './index.css'
import Passwordreset from "./components/passwordreset";
import Passwordforgot from "./components/passwordforgot";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ReactGA from 'react-ga';
import { createBrowserHistory } from 'history';


const history = createBrowserHistory();
history.listen(location => {
  ReactGA.set({ page: location.pathname }); // Update the user's current page
  ReactGA.pageview(location.pathname); // Record a pageview for the given page
});

const trackingId = "UA-162249992-1"; // Replace with your Google Analytics tracking ID
ReactGA.initialize(trackingId);
ReactGA.set({
  userId: {localStorage}
  // any data that is relevant to the user session
  // that you would like to track with google analytics
})

const isLoggedIn = () => {
  return Cookies.get("TOKEN_KEY");
};

// Protected Route
const SecuredRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      // ternary condition

      isLoggedIn() === undefined ? (
        
        <Route component={Login} />

        ) : (
        <Component {...props} />

      )
    }
  />
);
export default class App extends Component {
  render() {
    // const {pathname} = this.props.location;
    return (
        <Router>
          <Switch>
            <div>
            <Route exact path="/" component={Login} />
              <Route path="/register/:activated_token" component={Register} />
              <Route path="/login" component={Login} />
              <Route path="/password/reset/:token" component={Passwordreset} />
              <Route path="/password/forgot" component={Passwordforgot} />
              <SecuredRoute path="/dashboard" component={Dashboard} />
              <SecuredRoute exact path ="/movie/:id" component={MovDetail} />
              <SecuredRoute path="/movies" component={Movie} />
              <SecuredRoute path="/contact" component={Contact} />
              <SecuredRoute path="/invite" component={Invite} />
              <SecuredRoute exact path="/tv/:id" component={Video} />
            </div>
          </Switch>
          </Router>
      );
    }
  }
  

