import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Row, Col, Container} from 'react-bootstrap'
import './style.css'

let date = new Date();
date.setDate(date.getDate() +2)
let today = date.getFullYear()+'-'+(date.getMonth()+1)+'-'+(date.getDate())
date.setDate(date.getDate() -6)
let prior = date.getFullYear()+'-'+(date.getMonth()+1)+'-'+(date.getDate())

class NewShows extends Component {
    constructor() {
        super()
        this.state = {
            shows: []

        }
    }

componentDidMount() {
    fetch(`https://lp-server1.lastplay.tv/app/api/calendar?apikey=${process.env.REACT_APP_SONARR}&start=${prior}&end=${today}`)
    .then(res => res.json())
    .then((data)=> {
        this.setState({ 
            shows: data.sort((a, b) => b.airDate.localeCompare(a.airDate  ))
        })
        console.log(data)
    })
    .catch(console.log)
}



render() 
{
    return (
         <div className="tvHeader">
                         <Container style={{marginTop:"3%"}}>
                         <h2><span>New this week</span></h2> 
         <Row style={{marginTop:"3%"}}>
            {this.state.shows.map(show =>
            <>
            { show.hasFile === true &&
                    <Col md={6}lg={3} style={{marginBottom:"25px"}}>
                    
                    <Link to={`tv/${show.series.id}`}>
                    <b>{show.series.title}</b> <br />
                    <img className="img-fluid" src={`https://lp-server1.lastplay.tv/app/MediaCover/${show.series.id}/fanart.jpg`} alt={`${this.state.shows.title} poster`} /> <br />
            </Link>      <span style={{fontSize:'12px'}}>Season {show.seasonNumber} Episode {show.episodeNumber}
    </span>  
</Col> }
            </>    
            )}
        
        </Row>

        </Container>

          </div>
       

    )
    }
}
export default NewShows