import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Col, Row, Container} from 'react-bootstrap'
import './style.css'
import Header from './header'

class Movie extends Component {
    constructor() {
        super()
        this.state = {
            movies: []
        }
    }

componentDidMount() {
    fetch('https://lp-server1.lastplay.tv/mov/api/movie?apikey=9a3d6935c2574351ae3cb58f26f488cd',           
     { headers: {
        'Accept': 'application/json',
                  'Content-Type': ' application/json',
                  'X-API-SERVER': '85499f9f'
     }})
    .then(res => res.json())
    .then((data)=> {
       let comingSoon = data.filter(m => {
            return m.hasFile === false
        })
 
       data =  data.filter(m => {
           return m.hasFile === true
       })


        this.setState({ 
            movies: data.sort((a, b) => b.movieFile.dateAdded.localeCompare(a.movieFile.dateAdded  )),
            comingSoon: comingSoon
        })

        
    })
    .catch(console.log)
}


render() 
{
    return (<Container fluid>
<Header />
<Row md={12} style={{marginTop:"1%"}} className="justify-content-center ">
<img src={`${process.env.PUBLIC_URL}/lpmovies.png`}  alt="Movie Header"/>
</Row>
<Row className="justify-content-center ">

</Row>

     <div>
          <Row style={{marginTop:"3%"}}className="accordian justify-content-center">
            {this.state.movies.map(show => {
                return (
                    <Col xs={6} sm className='shows' key={`${show.id}`}>
                    <Link to={{
                        pathname:`/movie/${show.id}`, state: {
                                tmd: show.tmdbId}}}>
                        <img src={`https://lp-server1.lastplay.tv/mov/MediaCover/${show.id}/poster-250.jpg`} alt={`${show.title} poster`}/> <br />
                        <span>{show.title}</span>
                    </Link>
                </Col>)

            }

                )

            }
           
          </Row>
        </div>
        </Container>

    )
    }
}
export default Movie