import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import swal from "sweetalert";
import { Row, Container} from 'react-bootstrap'
import '../style.css'

console.log()
const PasswordResetSchema = Yup.object().shape({
  password: Yup.string()
    .required("Password is required"),
    confirmpassword: Yup.string()
    .required("Password si requiredd")
});

class Passwordforgot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: {},
      error_message: null,
    };
  }

  submitForm = async (values, history, token) => {
    await axios
      .put("/password/reset/" + token, values)
      .then(res => {
        if (res.data.result === "success") {
          swal("Success!", res.data.message, "success").then(value => {
            history.push("/login");
          });
        } else if (res.data.result === "error") {
          swal("Error!", res.data.message, "error");
        }
      })
      .catch(error => {
        console.log(error);
        swal("Error!", "Unexpected error", "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    isSubmitting,
  }) => {
    return (
      <Container>
<Row className="justify-content-center">
        <h1><b>Last</b>Play<span style={{fontSize:"14px"}}>.tv</span></h1>
        </Row>
        <Row className="justify-content-center">
        <img src={process.env.PUBLIC_URL + '/logo100.png'} alt="logo" /> 
        </Row>
        <Row className="justify-content-center">
<form style={{marginTop:"5%"}} onSubmit={handleSubmit}>
            <label htmlFor="password">Confirm your password and we will send you instructions to reset your password.</label><br />
            <Row className="justify-content-center">
           <input style={{textAlign:"center"}}
              onChange={handleChange}
              value={values.password}
              type="password"
              className={
                errors.password && touched.password
                  ? "form-controls2 is-invalid"
                  : "form-controls2"
              }
              id="password"
              placeholder="New Password"
            /><br /></Row>            <Row className="justify-content-center">

            {errors.password && touched.password ? (
              <small id="passwordHelp" class="text-danger">
                {errors.password}
              </small>
            ) : null}
        {/* /.card-body */}
           </Row>
            
           <Row className="justify-content-center">
  
        <input style={{textAlign:"center"}}
              onChange={handleChange}
              value={values.confirmpassword}
              type="password"
              className={
                errors.password && touched.password
                  ? "form-controls2 is-invalid"
                  : "form-controls2"
              }
              id="confirmpassword"
              placeholder="Confirm password"
            /> <br />        </Row>

            <Row className="justify-content-center">
            {errors.password && touched.password ? (
              <small id="passwordHelp" class="text-danger">
                {errors.password}
              </small>
            ) : null}
        {/* /.card-body */}
        </Row>

        <Row className="justify-content-center">

        <button
              type="submit"
              disabled={isSubmitting}
              class="btn btn-primary btn-group-lg"
            >
              Save new password
            </button>

        </Row>
        </form>
        </Row>
      </Container>
        
        

        



    );
  };

  render() {
    return (
<Container >
<Formik
                initialValues={{
                  password: "",
                  confirmpassword: "",
                  token: this.props.match.params["token"]
                }}
                onSubmit={(values, { setSubmitting }) => {
                  this.submitForm(
                    values,
                    this.props.history,
                    this.props.match.params["token"]
                  );
                  setSubmitting(true);
                }}
                validationSchema={PasswordResetSchema}
              >
                {props => this.showForm(props)}
              </Formik>


</Container>





    );
  }
}

export default Passwordforgot;