import React from 'react';
import { OverlayTrigger, Tooltip, } from 'react-bootstrap'
import swal from "sweetalert";
import axios from "axios";



class Report extends React.Component {
  constructor(props) {
    super(props);
  }
 
  renderTooltip = (props) => {
    return (
      <Tooltip id="button-tooltip" {...props}>
          Report
      </Tooltip>
    );
  }

  submitForm = (values, history) => {
    values = this.props
      axios
        .post('/report', values)
        .then(res => {
          if(res.data.result === "success") {
            swal("Thank you!", res.data.message, "success") } else if (res.data.result === "error") {
              swal("Error!", res.data.message, "error");
            }      })
        .catch(error => {
          console.log(error);
        });
    };
  

  render() 
  {
    return (
        <OverlayTrigger
        placement="right"
        delay={{ show: 250, hide: 400 }}
        overlay={this.renderTooltip}
      >
        <a onClick={this.submitForm} > <img src={process.env.PUBLIC_URL + '/flag.png' } alt="report" style={{width:"20px"}} /> </a>
      </OverlayTrigger>
    );
  }
}


export default Report;
 